.img-summary .summary .cart .quantity input[type=number]::-webkit-inner-spin-button,
.img-summary .summary .cart .quantity input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.img-summary .summary .cart .quantity .number_controlls {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  height: 50%;
  width: 30px;
  cursor: pointer;
}
.img-summary .summary .cart .quantity .minus {
  bottom: 0;
  top: inherit;
}
.img-summary .summary .cart .quantity .minus:after {
  position: absolute;
  top: 1px;
  right: 0;
  content: "";
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid #918552;
}
.img-summary .summary .cart .quantity .plus:after {
  right: 0;
  bottom: 1px;
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid #918552;
}

.woocommerce-description-product {
  width: 100%;
  padding-top: 6.25em;
  padding-left: 1.25em;
  padding-right: 1.25em;
}
@media screen and (min-width: 53.125em) {
  .woocommerce-description-product {
    padding-left: 8.75em;
    padding-right: 8.75em;
  }
}
.woocommerce-description-product h2 {
  font-size: 1.625em;
  margin-bottom: 1.15385em;
}
.woocommerce-description-product p {
  font-size: 1.125em;
}

.line-description {
  width: 100%;
  height: 1px;
  background: linear-gradient(to right, #918552, #f7dda9, #918552);
  margin-top: 6.25em;
  margin-bottom: 6.25em;
}

.related.products {
  width: 100%;
}
.related.products h2 {
  font-size: 1.625em;
  text-align: center;
}

.img-summary .summary .price {
  line-height: 1;
  padding-bottom: 40px;
}
.img-summary .summary .price del {
  color: #3c3c3b;
  font-size: 0.69231em;
}
.img-summary .summary .price .woocommerce-Price-amount {
  font-family: "Noto Serif", serif;
  font-weight: bold;
  font-size: 1.625em;
  color: #918552;
}
.img-summary .summary .price .woocommerce-Price-amount .int-price {
  font-size: 1.38462em;
}
